'use client';

import React, { useState, useEffect, useRef } from 'react';
import { DndContext, useDraggable, useDroppable, DragOverlay, MouseSensor, TouchSensor, useSensor, useSensors, DragEndEvent, DragStartEvent, DragOverEvent } from '@dnd-kit/core';
import { restrictToParentElement } from '@dnd-kit/modifiers';
import { Save, Type, Image as ImageIcon, Minus, Box, ArrowLeft, Move, Table as TableIcon, Palette, Hash, PaintBucket, AlignLeft, AlignCenter, AlignRight, Settings, FileText, Eye, X, LayoutTemplate } from 'lucide-react';
import Link from 'next/link';
import { v4 as uuidv4 } from 'uuid';
import InvoiceRenderer from '@/components/InvoiceRenderer';

// Types
type ElementType = 'text' | 'image' | 'box' | 'line' | 'table';

export interface GlobalSettings {
  padding: number;
  headerHeight: number;
  footerHeight: number;
}

export interface EditorElement {
  id: string;
  type: ElementType;
  x: number;
  y: number;
  w: number;
  h: number;
  content?: string;
  style?: React.CSSProperties;
  data?: any;
}

// Components
function SidebarItem({ type, icon: Icon, label }: { type: ElementType, icon: any, label: string }) {
  const { attributes, listeners, setNodeRef } = useDraggable({
    id: `sidebar-${type}`,
    data: { type, isSidebar: true },
  });

  return (
    <div ref={setNodeRef} {...listeners} {...attributes} className="group p-4 bg-white mb-3 border border-gray-200 rounded-xl cursor-grab hover:bg-blue-50 hover:border-blue-200 shadow-sm transition-all flex items-center gap-3">
      <div className="p-2 bg-gray-100 rounded-lg text-gray-600 group-hover:bg-blue-100 group-hover:text-blue-600 transition-colors">
         <Icon size={20} />
      </div>
      <span className="font-medium text-gray-700 group-hover:text-blue-700">{label}</span>
    </div>
  );
}

function DraggableElement({ element, isSelected, onSelect }: { element: EditorElement, isSelected: boolean, onSelect: (id: string) => void }) {
  const { attributes, listeners, setNodeRef, transform } = useDraggable({
    id: element.id,
    data: { ...element, isSidebar: false },
  });

  const style: React.CSSProperties = {
    position: 'absolute',
    top: element.y,
    left: element.x,
    width: element.w,
    height: element.h,
    transform: transform ? `translate3d(${transform.x}px, ${transform.y}px, 0)` : undefined,
    border: isSelected ? '2px solid #2563eb' : '1px dashed transparent',
    zIndex: isSelected ? 10 : 1,
    cursor: 'move',
    ...element.style,
  };

  const renderContent = () => {
    switch (element.type) {
      case 'text':
        return <div className="w-full h-full overflow-hidden leading-tight" style={{ color: element.style?.color || 'inherit' }}>{element.content || 'Texte'}</div>;
      case 'box':
        return (
          <div 
            className="w-full h-full" 
            style={{ 
              backgroundColor: element.style?.backgroundColor || '#e5e7eb', 
              borderColor: element.style?.borderColor || '#9ca3af',
              borderWidth: element.style?.borderWidth || '1px',
              borderStyle: 'solid',
              borderRadius: element.style?.borderRadius || '0px'
            }}
          ></div>
        );
      case 'line':
        return (
          <div className="w-full h-full flex items-center">
             <div className="w-full" style={{ height: element.style?.height || '2px', backgroundColor: element.style?.backgroundColor || 'black' }}></div>
          </div>
        );
      case 'table':
        return (
          <div className="w-full h-full overflow-hidden bg-white border border-gray-200">
             <div className="p-2 bg-gray-50 border-b text-xs text-center text-gray-500">Tableau d'articles (Aperçu)</div>
             <div className="p-2 text-xs space-y-2">
                <div className="flex justify-between border-b pb-1 font-bold">
                   <span>Description</span><span>Total</span>
                </div>
                <div className="flex justify-between text-gray-400">
                   <span>Article 1</span><span>100 €</span>
                </div>
                <div className="flex justify-between text-gray-400">
                   <span>Article 2</span><span>50 €</span>
                </div>
             </div>
          </div>
        );
      default:
        return <div>Element</div>;
    }
  };

  return (
    <div 
      ref={setNodeRef} 
      style={style} 
      {...listeners} 
      {...attributes}
      onClick={(e) => { e.stopPropagation(); onSelect(element.id); }}
      className={`hover:outline hover:outline-blue-300 group ${isSelected ? 'shadow-lg ring-2 ring-blue-500/20' : ''}`}
    >
      {renderContent()}
      {isSelected && (
        <div className="absolute -top-8 left-0 bg-blue-600 text-white text-[10px] px-2 py-1 rounded flex items-center gap-1 font-mono shadow-md">
           <Move size={10} />
           x:{Math.round(element.x)} y:{Math.round(element.y)}
        </div>
      )}
    </div>
  );
}

export default function BuilderClient({ template }: { template: any }) {
  const [isMounted, setIsMounted] = useState(false);
  const [elements, setElements] = useState<EditorElement[]>([]);
  const [globalSettings, setGlobalSettings] = useState<GlobalSettings>({ padding: 40, headerHeight: 0, footerHeight: 0 });

  useEffect(() => {
    setIsMounted(true);
    try {
      const parsed = JSON.parse(template.content);
      if (Array.isArray(parsed)) {
        setElements(parsed);
      } else {
        setElements(parsed.elements || []);
        setGlobalSettings(parsed.globalSettings || { padding: 40, headerHeight: 0, footerHeight: 0 });
      }
    } catch {
      setElements([]);
    }
  }, [template.content]);

  const [selectedId, setSelectedId] = useState<string | null>(null);
  const [activeDragId, setActiveDragId] = useState<string | null>(null);
  const [activeDragData, setActiveDragData] = useState<any>(null);
  const [saving, setSaving] = useState(false);
  const [showPreview, setShowPreview] = useState(false);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  const { setNodeRef: setCanvasRef, isOver } = useDroppable({ id: 'canvas' });

  const sensors = useSensors(
    useSensor(MouseSensor, { activationConstraint: { distance: 5 } }),
    useSensor(TouchSensor)
  );

  const handleDragStart = (event: DragStartEvent) => {
    setActiveDragId(event.active.id as string);
    setActiveDragData(event.active.data.current);
  };

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    setActiveDragId(null);
    setActiveDragData(null);

    if (!active.data.current?.isSidebar && !over) return; 

    const isSidebar = active.data.current?.isSidebar;

    if (isSidebar) {
      const type = active.data.current?.type as ElementType;
      
      let defaultW = 150;
      let defaultH = 50;
      if (type === 'line') { defaultW = 200; defaultH = 10; }
      if (type === 'table') { defaultW = 600; defaultH = 200; }
      if (type === 'box') { defaultW = 100; defaultH = 100; }

      const newElement: EditorElement = {
        id: uuidv4(),
        type,
        x: 50, 
        y: 50,
        w: defaultW,
        h: defaultH,
        content: type === 'text' ? 'Nouveau texte' : undefined,
        style: { fontSize: '14px', color: '#000000', backgroundColor: type === 'box' ? '#f3f4f6' : undefined }
      };
      setElements([...elements, newElement]);
      setSelectedId(newElement.id);
    } else {
      const id = active.id as string;
      const delta = event.delta;
      setElements(elements.map(el => {
        if (el.id === id) {
          return {
            ...el,
            x: el.x + delta.x,
            y: el.y + delta.y
          };
        }
        return el;
      }));
    }
  };

  const saveTemplate = async () => {
    setSaving(true);
    const contentToSave = JSON.stringify({
      elements,
      globalSettings
    });
    await fetch(`/api/templates/${template.id}`, {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ content: contentToSave }),
    });
    setSaving(false);
  };

  const updateSelected = (key: keyof EditorElement | 'style', value: any) => {
    if (!selectedId) return;
    setElements(elements.map(el => {
      if (el.id === selectedId) {
        if (key === 'style') {
            return { ...el, style: { ...el.style, ...value } };
        }
        return { ...el, [key]: value };
      }
      return el;
    }));
  };

  const alignElement = (alignment: 'left' | 'center' | 'right') => {
    if (!selectedId) return;
    const el = elements.find(e => e.id === selectedId);
    if (!el) return;

    const canvasWidth = 794;
    const padding = globalSettings.padding;
    let newX = el.x;

    switch (alignment) {
        case 'left':
            newX = padding;
            break;
        case 'center':
            newX = (canvasWidth - el.w) / 2;
            break;
        case 'right':
            newX = canvasWidth - el.w - padding;
            break;
    }

    updateSelected('x', newX);
  };

  const addVariable = (variable: string) => {
    if (!selectedId) return;
    const el = elements.find(e => e.id === selectedId);
    if (el && el.type === 'text') {
      const newContent = (el.content || '') + ` {{${variable}}}`;
      updateSelected('content', newContent);
    }
  };

  const selectedElement = elements.find(el => el.id === selectedId);

  if (!isMounted) return null;

  return (
    <DndContext sensors={sensors} onDragStart={handleDragStart} onDragEnd={handleDragEnd}>
      <div className="h-[calc(100vh-64px)] flex flex-col bg-gray-50">
        {/* Toolbar */}
        <div className="h-16 bg-white border-b border-gray-200 flex items-center justify-between px-6 shadow-sm z-20">
           <div className="flex items-center gap-4">
             <Link href="/dashboard" className="p-2 text-gray-500 hover:text-gray-900 hover:bg-gray-100 rounded-full transition-colors"><ArrowLeft size={20} /></Link>
             <div>
               <h1 className="font-bold text-gray-900">{template.name}</h1>
               <p className="text-xs text-gray-500">Éditeur visuel</p>
             </div>
           </div>
           <button 
             onClick={saveTemplate}
             className="bg-blue-600 text-white px-5 py-2 rounded-full font-medium flex items-center gap-2 hover:bg-blue-700 shadow-lg shadow-blue-600/20 transition-all active:scale-95 disabled:opacity-50 disabled:cursor-not-allowed"
             disabled={saving}
            >
             <Save size={18} /> {saving ? 'Enregistrement...' : 'Enregistrer'}
           </button>
           <button 
             onClick={() => setShowPreview(true)}
             className="ml-3 bg-gray-900 text-white px-5 py-2 rounded-full font-medium flex items-center gap-2 hover:bg-gray-800 shadow-lg transition-all active:scale-95"
            >
             <Eye size={18} /> Aperçu
           </button>
        </div>

        <div className="flex-1 flex overflow-hidden">
          {/* Sidebar */}
          <div className="w-72 bg-white border-r border-gray-200 p-6 overflow-y-auto z-10 shadow-[4px_0_24px_rgba(0,0,0,0.02)]">
            <h3 className="font-bold mb-6 text-gray-900 uppercase text-xs tracking-wider">Outils</h3>
            <SidebarItem type="text" icon={Type} label="Texte" />
            <SidebarItem type="box" icon={Box} label="Forme / Boîte" />
            <SidebarItem type="line" icon={Minus} label="Ligne" />
            <SidebarItem type="table" icon={TableIcon} label="Tableau d'articles" />
            
            <div className="mt-8 p-4 bg-blue-50 rounded-xl border border-blue-100">
               <p className="text-xs text-blue-700 leading-relaxed">
                 <strong>Astuce :</strong> Utilisez "Tableau d'articles" pour afficher automatiquement la liste des produits lors de la génération de la facture.
               </p>
            </div>
          </div>

          {/* Canvas Area */}
          <div className="flex-1 bg-gray-100/50 p-12 overflow-auto flex justify-center">
             <div 
               ref={setCanvasRef}
               id="invoice-canvas"
               onClick={() => setSelectedId(null)}
               className={`bg-white shadow-2xl relative transition-all duration-300 ${isOver ? 'ring-4 ring-blue-400/30 scale-[1.01]' : ''}`}
               style={{ 
                 width: '794px', 
                 height: '1123px', 
                 minWidth: '794px', 
                 minHeight: '1123px',
                 padding: `${globalSettings.padding}px`,
                 paddingTop: `${globalSettings.padding + globalSettings.headerHeight}px`,
                 paddingBottom: `${globalSettings.padding + globalSettings.footerHeight}px`
               }}
             >
               {/* Guides des marges (visuel seulement) */}
               <div className="absolute inset-0 pointer-events-none border border-dashed border-gray-200" 
                 style={{ 
                   top: globalSettings.padding + globalSettings.headerHeight, 
                   bottom: globalSettings.padding + globalSettings.footerHeight, 
                   left: globalSettings.padding, 
                   right: globalSettings.padding 
                 }} 
               />
               
               {elements.length === 0 && !isOver && (
                  <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                     <div className="text-center text-gray-300">
                        <Box size={48} className="mx-auto mb-2 opacity-20" />
                        <p className="text-lg font-medium">La page est vide</p>
                        <p className="text-sm">Commencez par glisser un élément</p>
                     </div>
                  </div>
               )}
               
               {isOver && (
                 <div className="absolute inset-0 bg-blue-50/10 pointer-events-none border-2 border-dashed border-blue-400 z-50 flex items-center justify-center">
                    <div className="bg-blue-500 text-white px-4 py-2 rounded-full shadow-lg font-medium animate-bounce">
                      Relâcher pour ajouter
                    </div>
                 </div>
               )}

               {elements.map(el => (
                 <DraggableElement 
                   key={el.id} 
                   element={el} 
                   isSelected={selectedId === el.id} 
                   onSelect={setSelectedId} 
                  />
               ))}
             </div>
          </div>

          {/* Properties Panel */}
          <div className="w-80 bg-white border-l border-gray-200 p-6 overflow-y-auto z-10 shadow-[-4px_0_24px_rgba(0,0,0,0.02)]">
            <h3 className="font-bold mb-6 text-gray-900 uppercase text-xs tracking-wider">Propriétés</h3>
            {selectedElement ? (
              <div className="space-y-6">
                <div className="pb-4 border-b border-gray-100">
                  <label className="block text-xs font-medium text-gray-500 uppercase mb-1">Type d'élément</label>
                  <div className="text-gray-900 font-medium capitalize flex items-center gap-2">
                     <div className="w-2 h-2 rounded-full bg-blue-500"></div>
                     {selectedElement.type === 'box' ? 'Boîte' : selectedElement.type === 'table' ? 'Tableau' : selectedElement.type}
                  </div>
                </div>
                
                {/* Alignement Position (Global) */}
                <div className="pb-4 border-b border-gray-100">
                   <label className="block text-xs font-medium text-gray-500 uppercase mb-2">Positionnement</label>
                   <div className="flex bg-gray-50 rounded-lg p-1 border border-gray-200">
                      <button 
                        onClick={() => alignElement('left')}
                        title="Aligner à gauche"
                        className="flex-1 py-1.5 flex justify-center rounded hover:bg-white hover:shadow-sm text-gray-500 hover:text-gray-900 transition-all"
                      >
                        <LayoutTemplate size={16} className="rotate-180" />
                      </button>
                      <button 
                        onClick={() => alignElement('center')}
                        title="Centrer horizontalement"
                        className="flex-1 py-1.5 flex justify-center rounded hover:bg-white hover:shadow-sm text-gray-500 hover:text-gray-900 transition-all"
                      >
                        <LayoutTemplate size={16} />
                      </button>
                      <button 
                        onClick={() => alignElement('right')}
                        title="Aligner à droite"
                        className="flex-1 py-1.5 flex justify-center rounded hover:bg-white hover:shadow-sm text-gray-500 hover:text-gray-900 transition-all"
                      >
                        <LayoutTemplate size={16} />
                      </button>
                   </div>
                </div>

                {/* Coordonnées et Dimensions */}
                <div className="grid grid-cols-2 gap-4">
                   <div className="space-y-1">
                    <label className="block text-xs font-medium text-gray-500">Position X</label>
                    <input type="number" value={selectedElement.x} onChange={(e) => updateSelected('x', parseInt(e.target.value))} className="w-full bg-gray-50 border border-gray-200 rounded-md px-3 py-2 text-sm text-gray-900" />
                   </div>
                   <div className="space-y-1">
                    <label className="block text-xs font-medium text-gray-500">Position Y</label>
                    <input type="number" value={selectedElement.y} onChange={(e) => updateSelected('y', parseInt(e.target.value))} className="w-full bg-gray-50 border border-gray-200 rounded-md px-3 py-2 text-sm text-gray-900" />
                   </div>
                   <div className="space-y-1">
                    <label className="block text-xs font-medium text-gray-500">Largeur</label>
                    <input type="number" value={selectedElement.w} onChange={(e) => updateSelected('w', parseInt(e.target.value))} className="w-full bg-gray-50 border border-gray-200 rounded-md px-3 py-2 text-sm text-gray-900" />
                   </div>
                   <div className="space-y-1">
                    <label className="block text-xs font-medium text-gray-500">Hauteur</label>
                    <input type="number" value={selectedElement.h} onChange={(e) => updateSelected('h', parseInt(e.target.value))} className="w-full bg-gray-50 border border-gray-200 rounded-md px-3 py-2 text-sm text-gray-900" />
                   </div>
                </div>

                {/* Styles de base */}
                <div className="space-y-4 border-t border-gray-100 pt-4">
                   <h4 className="text-xs font-bold text-gray-900 uppercase tracking-wider mb-2 flex items-center gap-2">
                     <Palette size={12} /> Apparence
                   </h4>
                   
                   {/* Couleur de texte (pour Text) */}
                   {selectedElement.type === 'text' && (
                     <div>
                        <label className="block text-xs font-medium text-gray-500 mb-1">Couleur du texte</label>
                        <div className="flex items-center gap-2">
                           <input 
                             type="color" 
                             value={selectedElement.style?.color || '#000000'} 
                             onChange={(e) => updateSelected('style', { ...selectedElement.style, color: e.target.value })} 
                             className="h-8 w-8 rounded cursor-pointer border border-gray-200 p-0.5"
                           />
                           <span className="text-xs text-gray-500">{selectedElement.style?.color || '#000000'}</span>
                        </div>
                     </div>
                   )}

                   {/* Couleur de fond (pour Box, Line, Table) */}
                   {(selectedElement.type === 'box' || selectedElement.type === 'line' || selectedElement.type === 'table') && (
                     <div>
                        <label className="block text-xs font-medium text-gray-500 mb-1">
                           {selectedElement.type === 'line' ? 'Couleur de la ligne' : 'Couleur de fond'}
                        </label>
                        <div className="flex items-center gap-2">
                           <input 
                             type="color" 
                             value={selectedElement.style?.backgroundColor || (selectedElement.type === 'line' ? '#000000' : '#ffffff')} 
                             onChange={(e) => updateSelected('style', { ...selectedElement.style, backgroundColor: e.target.value })} 
                             className="h-8 w-8 rounded cursor-pointer border border-gray-200 p-0.5"
                           />
                        </div>
                     </div>
                   )}

                   {/* Bordure (pour Box) */}
                   {selectedElement.type === 'box' && (
                     <>
                       <div>
                          <label className="block text-xs font-medium text-gray-500 mb-1">Couleur Bordure</label>
                          <input 
                             type="color" 
                             value={selectedElement.style?.borderColor || '#000000'} 
                             onChange={(e) => updateSelected('style', { ...selectedElement.style, borderColor: e.target.value })} 
                             className="h-8 w-8 rounded cursor-pointer border border-gray-200 p-0.5"
                           />
                       </div>
                       <div>
                          <label className="block text-xs font-medium text-gray-500 mb-1">Arrondi (px)</label>
                          <input 
                             type="number" 
                             value={parseInt(selectedElement.style?.borderRadius as string) || 0} 
                             onChange={(e) => updateSelected('style', { ...selectedElement.style, borderRadius: `${e.target.value}px` })} 
                             className="w-full bg-gray-50 border border-gray-200 rounded-md px-3 py-2 text-sm text-gray-900"
                           />
                       </div>
                     </>
                   )}
                </div>

                {/* Contenu Texte & Variables */}
                {selectedElement.type === 'text' && (
                  <div className="space-y-4 border-t border-gray-100 pt-4">
                    <h4 className="text-xs font-bold text-gray-900 uppercase tracking-wider mb-2 flex items-center gap-2">
                     <Type size={12} /> Contenu
                    </h4>
                    
                    <div className="space-y-1">
                       <label className="block text-xs font-medium text-gray-500">Alignement</label>
                       <div className="flex bg-gray-50 rounded-lg p-1 border border-gray-200">
                          <button 
                            onClick={() => updateSelected('style', { ...selectedElement.style, textAlign: 'left' })}
                            className={`flex-1 py-1 flex justify-center rounded ${(!selectedElement.style?.textAlign || selectedElement.style?.textAlign === 'left') ? 'bg-white shadow-sm text-blue-600' : 'text-gray-400 hover:text-gray-600'}`}
                          >
                            <AlignLeft size={16} />
                          </button>
                          <button 
                            onClick={() => updateSelected('style', { ...selectedElement.style, textAlign: 'center' })}
                            className={`flex-1 py-1 flex justify-center rounded ${selectedElement.style?.textAlign === 'center' ? 'bg-white shadow-sm text-blue-600' : 'text-gray-400 hover:text-gray-600'}`}
                          >
                            <AlignCenter size={16} />
                          </button>
                          <button 
                            onClick={() => updateSelected('style', { ...selectedElement.style, textAlign: 'right' })}
                            className={`flex-1 py-1 flex justify-center rounded ${selectedElement.style?.textAlign === 'right' ? 'bg-white shadow-sm text-blue-600' : 'text-gray-400 hover:text-gray-600'}`}
                          >
                            <AlignRight size={16} />
                          </button>
                       </div>
                    </div>

                    <div className="space-y-1">
                      <label className="block text-xs font-medium text-gray-500">Texte</label>
                      <textarea 
                        value={selectedElement.content} 
                        onChange={(e) => updateSelected('content', e.target.value)}
                        className="w-full bg-gray-50 border border-gray-200 rounded-md px-3 py-2 text-sm text-gray-900 min-h-[80px]"
                        rows={4}
                      />
                    </div>
                    
                    <div>
                       <label className="block text-xs font-medium text-gray-500 mb-2">Insérer une variable</label>
                       <div className="flex flex-wrap gap-2">
                          <button onClick={() => addVariable('client')} className="px-2 py-1 bg-blue-50 text-blue-600 text-xs rounded border border-blue-100 hover:bg-blue-100">Client</button>
                          <button onClick={() => addVariable('date')} className="px-2 py-1 bg-blue-50 text-blue-600 text-xs rounded border border-blue-100 hover:bg-blue-100">Date</button>
                          <button onClick={() => addVariable('number')} className="px-2 py-1 bg-blue-50 text-blue-600 text-xs rounded border border-blue-100 hover:bg-blue-100">Numéro</button>
                       </div>
                    </div>

                    <div className="space-y-1">
                       <label className="block text-xs font-medium text-gray-500">Taille de police (px)</label>
                       <input 
                         type="text" 
                         value={selectedElement.style?.fontSize || ''} 
                         onChange={(e) => updateSelected('style', { ...selectedElement.style, fontSize: e.target.value })} 
                         className="w-full bg-gray-50 border border-gray-200 rounded-md px-3 py-2 text-sm text-gray-900"
                       />
                    </div>
                  </div>
                )}

                 <div className="pt-4 border-t border-gray-100 mt-4">
                   <button 
                     onClick={() => {
                       setElements(elements.filter(e => e.id !== selectedId));
                       setSelectedId(null);
                     }}
                     className="w-full bg-red-50 text-red-600 border border-red-100 py-2.5 rounded-lg hover:bg-red-100 hover:border-red-200 transition-colors font-medium text-sm flex items-center justify-center gap-2"
                   >
                     Supprimer l'élément
                   </button>
                 </div>
              </div>
            ) : (
              <div className="space-y-6">
                <div className="flex items-center gap-2 pb-4 border-b border-gray-100">
                   <div className="p-2 bg-gray-100 rounded-lg text-gray-600">
                      <Settings size={20} />
                   </div>
                   <div>
                      <h3 className="font-bold text-gray-900">Paramètres globaux</h3>
                      <p className="text-xs text-gray-500">Configuration du document</p>
                   </div>
                </div>

                <div className="space-y-4">
                   <div className="space-y-1">
                      <label className="block text-xs font-medium text-gray-500">Marge de page (px)</label>
                      <input 
                         type="number" 
                         value={globalSettings.padding} 
                         onChange={(e) => setGlobalSettings({ ...globalSettings, padding: parseInt(e.target.value) || 0 })} 
                         className="w-full bg-gray-50 border border-gray-200 rounded-md px-3 py-2 text-sm text-gray-900"
                       />
                   </div>
                   
                   <div className="space-y-1">
                      <label className="block text-xs font-medium text-gray-500">Hauteur Header (px)</label>
                      <input 
                         type="number" 
                         value={globalSettings.headerHeight} 
                         onChange={(e) => setGlobalSettings({ ...globalSettings, headerHeight: parseInt(e.target.value) || 0 })} 
                         className="w-full bg-gray-50 border border-gray-200 rounded-md px-3 py-2 text-sm text-gray-900"
                       />
                       <p className="text-[10px] text-gray-400">Espace réservé en haut de page</p>
                   </div>

                   <div className="space-y-1">
                      <label className="block text-xs font-medium text-gray-500">Hauteur Footer (px)</label>
                      <input 
                         type="number" 
                         value={globalSettings.footerHeight} 
                         onChange={(e) => setGlobalSettings({ ...globalSettings, footerHeight: parseInt(e.target.value) || 0 })} 
                         className="w-full bg-gray-50 border border-gray-200 rounded-md px-3 py-2 text-sm text-gray-900"
                       />
                       <p className="text-[10px] text-gray-400">Espace réservé en bas de page</p>
                   </div>
                </div>

                <div className="p-4 bg-yellow-50 rounded-xl border border-yellow-100 mt-4">
                   <div className="flex gap-2">
                      <FileText className="text-yellow-600 shrink-0" size={16} />
                      <p className="text-xs text-yellow-700 leading-relaxed">
                        Ces marges définissent la zone de sécurité pour l'impression. Les éléments en dehors pourraient être coupés.
                      </p>
                   </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
      <DragOverlay>
        {activeDragId ? (
            <div className="bg-blue-600 p-3 rounded-lg text-white shadow-xl font-medium flex items-center gap-2">
               {activeDragData?.type === 'table' ? <TableIcon size={16}/> : <Box size={16}/>}
               Ajouter {activeDragData?.type}
            </div>
        ) : null}
      </DragOverlay>
    </DndContext>
  );
}
